import controllers from '../controllers'

export default function ($elm, attrs) {

    if ($elm.data('ic-ctrl-scope')) return; // dom controller factory

    let ctrlName = $elm.attr('ic-ctrl');

    if (ctrlName) {
        let $parent = $elm.parent().closest('[ic-ctrl]');
        let parentName = $parent.length ? $parent.attr('ic-ctrl') : '';
        //controllers.exec(ctrlName, controllers.get(parentName), $elm);  // ,dom
        controllers.exec(ctrlName, $parent.data('ic-ctrl-scope'), $elm);
    }

}
