import _ from 'lodash'
import $ from 'jquery'
import brick from './export'
import parser from './parser'

export default function createRender (root) {

    root = root.cloneNode(true);

    // dom
    (function fx (node) {

        parser(node);

        let children = $(node).contents();
        let child;
        let i = 0;
        while (child = children.eq(i)[0]) {
            i++;
            fx(child);
        }

    })(root);

    let _tpl = $(root).html()
        .replace(/&lt;/g, '<')
        .replace(/&gt;/g, '>')
        .replace(/\b(ic-)(?=href|src|style|class|data|value)/g, '')
        //ic-has-[prop]
        .replace(/\bic-(\w+-)?(checked|disabled|selected|enabled)\s*=\s*"\s*((?:[^"]|\\")+)["]/g, function (m, $1, $2, $3) {
            if ($1 === 'has-') return m;
            $1 = $1 ? 'ic-' + $1 : '';  //  ic-tab-checked
            //$3 = $3.replace(/^(?:"|')|(?:"|')$/g,'');
            return ' <% if(?3){ %> ?2 <% } %> '.replace('?3', $3).replace('?2', $1 + $2);
        })
        // ic-has-[prop]   =>  1: ic-has-checked  2: ic-has-tab-checked
        .replace(/\bic-has-([-_\w]+)\s*=\s*(["])((?:[^"]|\\["])+)\2/img, function (m, $1, $2, $3) {
            //$1 =>    $3 => 
            // console.log($1);
            // console.log($3);
            // =>  1: ic-has-checked  2: ic-has-tab-checked
            // :  ic-has-checked, ic-has-tab-checked; ic-has-ic-tab-checked, ic
            // $1 = /^checked|disabled|selected|enabled$/.test($1) ? $1 : 'ic-' + $1;
            return ' <% if(?3){ %> ?1 <% } %> '
                .replace('?3', $3)
                .replace('?1', $1);
        })
        .replace(/&amp;&amp;/g, '&&');

    brick.get('debug') && console.log(_tpl);

    let tpl_fn;

    try {
        tpl_fn = _.template(_tpl);
    } catch (e) {
         console.error(e, _tpl);
         throw new Error('');
    }

    tpl_fn._tpl_ = _tpl;
    return tpl_fn;

}
