/**
 * ic-select  checkbox or radio 
 * ic-select-cla  , selected
 * [ic-select][ic-select-item]   jQuery
 * [ic-select-item] 
 * [selected] 
 * [ic-select-type] select,or checkbox : radio  radio
 * [ic-select-auto] change, ,  ic-select-auto="false"
 * [ic-select-order] false;  ic-select-order="true"
 * [ic-select-input] input, 
 * Created by j on 18/7/18.
 */

import $ from 'jquery'
import brick from '../core/export'

export default function ($elm) {
    //console.info('exec ic-select', $elm[0]);

    let name = $elm.attr('ic-select');
    let cla = $elm.attr('ic-select-cla') || brick.get('ic-select-cla') || 'selected';
    let s_item = $elm.attr('ic-select-item') || '[ic-select-item]';
    let type = $elm.attr('ic-select-type') || 'radio';
    let isAuto = $elm.attr('ic-select-auto');  // change, 
    let isOrder =$elm.attr('ic-select-order');
    let onChange = $elm.icPp2('ic-select-on-change');
    let $input = $(`input[ic-select-input="${name}"]`);
    let $items = $elm.find(s_item);

    let time = 0;  // change
    let checkBoxValues = [];

    function count() {
        setTimeout(() => {
            time += 1;
        }, 30);
    }

    // item, selectoritem
    if (!$items.length) {
        $items = $elm.find('>*').each(function () {
            $(this).attr('ic-select-item', +new Date);
        });
    }

    let $selected = $items.filter('[selected]').addClass(cla);
    if (!$selected.length) {
        $selected = $items.filter('.' + cla);
    }

    let callback;  //  radio  checkbox 
    if (type === 'checkbox') {

        let setVal1 = () => {
            let values = [];
            $items.filter('.' + cla).each(function () {
                let val = $(this).attr('ic-val');
                val && values.push(val);
            });
            checkBoxValues = values;
            $elm.data('ic-val', values);
            return values;
        }

        let setVal2 = (selected, val) => {
            //   
            if (selected) {
                checkBoxValues.push(val);
            } else {
                let index = checkBoxValues.indexOf(val);
                if (index !== -1) {
                    checkBoxValues.splice(index, 1);
                }
            }
            $elm.data('ic-val', checkBoxValues);
            return checkBoxValues;
        };

        let setVal = isOrder ? setVal2 : setVal1;

        // 
        setVal1();

        callback = function () {
            let $th = $(this);
            let change = $th.toggleClass(cla).attr('ic-val');
            let selected = $th.hasClass(cla);

            let values = setVal(selected, change);
            //let values = setVal();
            let msg = { name: name, value: values, change, selected, time };

            console.log('ic-select.change', msg, checkBoxValues);

            $elm.trigger('ic-select.change', msg);
            $elm.trigger('change', msg);
            onChange && onChange.apply($elm, [msg]);
            count();
        }
    } else {

        // 
        $elm.attr('ic-val', $selected.attr('ic-val') || '');

        callback = function () {
            let $th = $(this);
            let isHas = $th.hasClass(cla);
            let val = '';
            let selected; //
            let change = $th.attr('ic-val');
            if (isHas) {
                $th.removeClass(cla);
                selected = false;
            } else {
                $items.removeClass(cla);
                $th.addClass(cla);
                val = change;
                selected = true;
            }
            let msg = { name: name, value: val, change, selected, time };
            // input
            $input.val(val);
            $elm.attr('ic-val', val);
            /*            if(typeof val === 'undefined'){
                            $elm.removeAttr('ic-val');
                        }else{
                            $elm.attr('ic-val', val);
                        }*/

            // console.log('ic-select.change', msg);
            $elm.trigger('ic-select.change', msg);
            $elm.trigger('change', msg);
            onChange && onChange.apply($elm, [msg]);
            count();
        }
    }

    $elm.on('click', s_item, callback);

    // tplcla
    if (!isAuto) {
        $selected.not('.' + cla).click();
        count();
    } else {

        // cla
        $selected.addClass(cla);
    }

}
